.container__cards {
  padding-top: 60px;
  padding-bottom: 60px;
  margin-top: 219px;
}

.gallery__list {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px 0;

  @media (min-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
  }
  @media (min-width: 1024px) {
    grid-template-columns: repeat(3, 1fr);
    gap: 32px 17px;
  }
}

.library__info {
  font-size: 24px;
  text-align: center;
}
